.main-footer p{
	margin-bottom: 0 !important;
}
.main-footer {
	-webkit-transition: 0.5s ease-in-out; /* css的transition允许css的属性值在一定的时间内从一个状态平滑的过渡到另一个状态 */
	-moz-transition: 0.5s ease-in-out; /*这里为了兼容其他浏览器*/
	-o-transition: 0.5s ease-in-out;
	width: 100%;
	text-align: center;
	font-size: 13px;
	color: #c0c4cc;
	padding: 0;
}

