<template>
  <v-bottom-navigation v-model="select" app color="#409EFF" @change="selectChange" active-class="my-btn-active">
    <v-container>
      <v-row>
        <v-col cols="4" align="center">
          <v-btn value="home">
            <span>主页</span>
            <v-icon>el-icon-house</v-icon>
          </v-btn>
        </v-col>
        <v-col cols="4" align="center">
          <v-btn value="join">
            <span>参与</span>
            <v-icon class="iconfont">icon-chengchangjilu</v-icon>
          </v-btn>
        </v-col>
        <v-col cols="4" align="center">
          <v-btn value="user">
            <span>我的</span>
            <v-icon>el-icon-user</v-icon>
          </v-btn>
        </v-col>
      </v-row>
    </v-container>
  </v-bottom-navigation>
</template>

<script>
export default {
  name: 'XcBottomNavigation',
  data: () => ({
    select: "home"
  }),
  methods: {
    selectChange(select) {
      if (this.$route.name !== select && this.$route.name !== "dialogPage") {
        this.$router.push(select);
      }
    }
  },
  beforeMount: function () {
    this.$router.afterEach(to => {
      if(to.name !== "dialogPage") {
        this.$data.select = to.name;
      }
    });
    this.$data.select = this.$route.name
  }
};
</script>

<style>
.v-btn {
  border-radius: 20px !important;
}

.v-btn--active::before {
  opacity: 0 !important;
}
</style>
