<template>

    <div class="yb-type-item" :id="'yb-type-item-id-'+data.id">
      <div class="yb-type-item-header">
        <div class="yb-type-item-tag"/>
        <div class="yb-type-item-name" v-text="data.name"/>
        <div class="yb-type-item-more"
             @click="()=>{this.$router.push('dialogPage/type_item?id='+data.id+'&title='+data.name+'&status=1');}">查看更多
          <v-icon size="20">el-icon-arrow-right</v-icon>
        </div>
      </div>
      <XcProject v-show="(data.YBProjectItems.length > 0)" v-for="(item,index) in data.YBProjectItems" :data="controlShowDivider(item, index)" :key="index"
                 @onClick="()=>{$router.push('dialogPage/detail?title='+item.name+'&id='+item.id);}" :type="type"/>
      <div v-if="data.YBProjectItems.length === 0" class="mb-5">暂无活动</div>
    </div>
</template>

<script>
import XcProject from '../components/project.vue';

export default {
  data: () => ({
    loader: true
  }),
  methods: {
    controlShowDivider(item, index) {
      item['showDivider'] = this.data.YBProjectItems.length > index + 1;
      return item;
    }
  },
  props: {
    data: {
      type: Object
    },
    type: {
      type: String
    }
  },
  components: {
    XcProject
  }
};
</script>

