<template>
  <v-container class="yb-project" @click="()=>{this.$emit('onClick')}">
    <v-row>
      <v-col :cols="5">
        <v-row>
          <v-img :aspect-ratio="16/9" class="yb-project-img" :src="data.banner[0].img"/>
          <el-tag v-if="type === 'check' || type === 'join' || type === 'search'" class="yb-project-tag" size="mini" :type="getType(data)" effect="dark">{{ data.type.name }}</el-tag>
        </v-row>
        <v-row class="justify-center mt-4">
          <div class="yb-project-status" :style="{'background-color':color}">
            {{ checkStatus(data.status) }}
          </div>
        </v-row>
      </v-col>
      <v-col class="ml-5">
        <v-row class="yb-project-name" v-text="data.name"/>
        <v-row>
          <v-icon size="16">el-icon-location-outline</v-icon>
          <div class="body-1 yb-project-location ml-1">{{ data.location }}</div>
        </v-row>
        <v-row>
          <v-icon size="16">el-icon-time</v-icon>
          <div class="body-1 yb-project-start-time ml-1">{{ convertTime(data.startTime) }}</div>
        </v-row>
        <v-row>
          <v-icon size="16">el-icon-user</v-icon>
          <div class="body-1 yb-project-limit-num ml-1">{{ data.currentNum }}/{{ data.limitNum }}人</div>
        </v-row>
      </v-col>
    </v-row>
    <v-row>
      <div v-if="data.showDivider" class="yb-project-divider"/>
    </v-row>
  </v-container>
</template>

<script>
import {Tag} from 'element-ui';
import Vue from 'vue';
Vue.use(Tag);

export default {
  data: () => ({
    show: false,
    color: "#000"
  }),
  methods: {
    convertTime(time) {
      let date = new Date(time);
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      m = m < 10 ? '0' + m : m;
      let d = date.getDate();
      d = d < 10 ? ('0' + d) : d;
      return y + '-' + m + '-' + d;
    },
    checkStatus(status) {
      if (status === "-9223372036854775808" || status === "-9223372036854775807" || status === "-9223372036854775806") {
        //未开始未审核，进行中未审核，已结束未审核
        this.$data.color = "#E6A23C"
        return "未审核"
      } else if ((status === "0" || status === "1" || status === "2"
          || status === "4" || status === "5" || status === "6"
          || status === "12" || status === "13" || status === "14") && (this.$props.type === "check" || this.$props.type === "my")) {
        this.$data.color = "#409EFF"
        return "已通过"
      } else if (status === "0" && this.$props.type !== "check") {
        //未开始
        this.$data.color = "#409EFF"
        return "未开始"
      } else if (status === "1") {
        //进行中
        this.$data.color = "#E6A23C"
        return "进行中"
      } else if (status === "2") {
        //已结束
        this.$data.color = "#909399"
        return "已结束"
      } else if (status === "4" || status === "5" || status === "6") {
        //已报名未开始，已报名进行中，已报名已结束
        this.$data.color = "#F56C6C"
        if (status === "6") {
          this.$data.color = "#909399"
        }
        return "未签到"
      } else if (status === "12" || status === "13" || status === "14") {
        //已签到未开始，已签到进行中，已签到已结束
        this.$data.color = "#67C23A"
        return "已签到"
      }
      return status;
    },
    getType(data){
      return data.type.name === '义工' ? 'success':data.type.name === '讲座' ? 'warning':''
    }
  },
  props: {
    data: {
      type: Object
    },
    type: {
      type: String
    }
  },
  components: {}
};
</script>

