import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/home',
        name: 'home',
        component: Home
    },
    {
        path: '/join',
        name: 'join',
        component: () => import( /* webpackChunkName: "about" */ '../views/Join.vue')
    },
    {
        path: '/user',
        name: 'user',
        component: () => import( /* webpackChunkName: "about" */ '../views/User.vue')
    },
    {
        path: '/dialogPage/:type',
        name: 'dialogPage',
        component: () => import( /* webpackChunkName: "about" */ '../views/DialogPage.vue')
    },
    {
        path: '*',
        redirect: {
            name: 'home'
        }
    }
]

const router = new VueRouter({
    routes
})

export default router
