import Vue from "vue"
import timeUtils from "@/utils/timeUtils";
import {Message} from "element-ui";

export default {
    getBanner(data, callback) {
        Vue.axios.post('/banner/getBanner').then(value => {
            if (value.data.code === 200) {
                data.banners = value.data.data;
                if (callback != null) {
                    callback(value.data.data)
                }
            }
        });
    },
    getTypeItemList(data) {
        Vue.axios.post('/project/getTypeItemList').then(value => {
            if (value.data.code === 200) {
                data.data = value.data.data;
            }
        })
    },
    getUserInfo(data) {
        Vue.axios.post('/user/getUser').then(value => {
            if (value.data.code === 200) {
                data.user = value.data.data;
                switch (data.user.power) {
                    case "ADMIN":
                        data.expendData.push({
                            icon: "#icon-renwu", content: "项目审核", onclick: (_this) => {
                                _this.$router.push('dialogPage/checkProject?title=项目审核')
                            }
                        });
                        data.expendData.push({
                            icon: "#icon-tupian", content: "Banner管理", onclick: (_this) => {
                                _this.$router.push('dialogPage/bannerImg?title=Banner管理')
                            }
                        });
                        data.expendData.push({
                            icon: "#icon-shiyan", content: "我的项目", onclick: (_this) => {
                                _this.$router.push("dialogPage/myProject?title=我的项目")
                            }
                        });
                        data.expendData.push({
                            icon: "#icon-more", content: "用户管理", onclick: (_this) => {
                                _this.$router.push("dialogPage/userControl?title=用户管理")
                            }
                        });
                        data.addProjectShow = true;
                        break;
                    case "TEACHER":
                        data.expendData.push({
                            icon: "#icon-shiyan", content: "我的项目", onclick: (_this) => {
                                _this.$router.push("dialogPage/myProject?title=我的项目")
                            }
                        });
                        data.addProjectShow = true;
                        break;
                }
                data.expendData.push({
                    icon: "#icon-fenlei", content: "关于易报名", onclick: (_this, Message) => {
                        this.getOne((message) => {
                            Message(message)
                        });
                    }
                });
            }
        })
    },
    getTypeItem(data, type) {
        Vue.axios.post('/project/getTypeItem').then(value => {
            if (value.data.code === 200) {
                data.type = value.data.data;
                if(type == null) {
                    data.typeName = data.type[0].name
                    data.form.type = data.type[0]
                }
            }
        })
    },
    createProjectItem(data, message, callback, errorBack) {
        data.startTime = timeUtils.dateFormat("yyyy-MM-dd hh:mm:SS", new Date(data.startTime))
        data.endTime = timeUtils.dateFormat("yyyy-MM-dd hh:mm:SS", new Date(data.endTime))
        let param = new FormData()
        param.append("projectItemJson", JSON.stringify(data))
        Vue.axios.post("/project/createProjectItem", param).then(value => {
            if (value.data.code === 200) {
                callback(value.data.data);
            } else {
                errorBack(value.data.data)
                data.startTime = new Date(data.startTime).getTime()
                data.endTime = new Date(data.endTime).getTime()
            }
        }).catch(reason => {
            message.error(reason);
            data.startTime = new Date(data.startTime).getTime()
            data.endTime = new Date(data.endTime).getTime()
        })
    },
    uploadImage(data, callback) {
        let param = new FormData()  // 创建form对象
        param.append('file', data, data.name)  // 通过append向form对象添加数据
        Vue.axios.post('/image/upload', param, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }).then(response => {
            if (response.data.code === 200) {
                callback(response.data.data)
            }
        })
    },
    getOne(message) {
        Vue.axios.post("https://api.xygeng.cn/one").then(data => {
            if (data.data.code === 200) {
                message(data.data.data.content);
            }
        })
    },
    selectProjectUnClick(data, check) {
        let param = new FormData();
        param.append("pagePojoJson", JSON.stringify({pageIndex: Math.floor(data.data.length / 10)}))
        param.append("check", check)
        Vue.axios.post("/project/selectProjectUnCheck", param).then(response => {
            if (response.data.code === 200) {
                if (JSON.parse(param.get("pagePojoJson")).pageIndex === Math.floor(data.data.length / 10)) {
                    response.data.data.forEach(item => {
                        data.data.push(item)
                    });
                }
                if (response.data.data.length < 10) {
                    data.disable = true;
                }
            }
        })
    },
    getProjectItemDetail(data, id, type) {
        let param = new FormData()
        param.append("id", id)
        Vue.axios.post('/project/getOldProjectItemDetail', param).then(response => {
            if (response.data.code === 200 && type == null) {
                data.$data.detail = response.data.data;
                this.selectComment(data.$data, data.$data.detail, null)
            } else {
                data.$data.form.id = response.data.data.id;
                data.$data.form.createTime = response.data.data.createTime;
                data.$data.form.currentNum = response.data.data.currentNum;
                data.$data.form.name = response.data.data.name;
                data.$data.form.type = response.data.data.type;
                data.$data.typeName = response.data.data.type.name;
                data.$data.form.location = response.data.data.location;
                data.$data.form.startTime = response.data.data.startTime;
                data.$data.form.endTime = response.data.data.endTime;
                data.$data.form.limitNum = response.data.data.limitNum;
                data.$data.form.viewSum = response.data.data.viewSum;
                data.$data.form.banner = response.data.data.banner;
                data.$data.form.banner.forEach(item => {
                    data.$data.bannerList.push({name: item.img, url: item.img})
                })
                let html = ''
                response.data.data.info.forEach(item => {
                    if(item.content != null){
                        html += '<section>'+item.content+'</section>'
                    }
                    if(item.url != null){
                        html += '<section><img src="'+item.url+'"/></section>'
                    }
                })
                data.$data.zxEditor.setHtml(html);
            }
        })
    },
    selectComment(data, project, page) {
        let param = new FormData()
        param.append("ybProjectItem", JSON.stringify(project))
        if (page != null) {
            param.append("page", JSON.stringify(page))
        }
        Vue.axios.post("/comment/selectComment", param).then(response => {
            if (response.data.code === 200) {
                data.comments = response.data.data;
            }
        })
    },
    createComment(data, project, myCommentData, comment) {
        let param = new FormData()
        param.append("ybProjectItem", JSON.stringify(project))
        param.append("comment", JSON.stringify(comment))
        Vue.axios.post("/comment/createComment", param).then(response => {
            if (response.data.code === 200) {
                data.data.push(response.data.data)
                myCommentData.myComment = ''
            }
        })
    },
    deleteComment(data, project, comment) {
        let param = new FormData()
        param.append("ybProjectItem", JSON.stringify(project))
        param.append("comment", JSON.stringify(comment))
        Vue.axios.post("/comment/deleteComment", param).then(response => {
            if (response.data.code === 200) {
                let index = 0
                for (let i = 0; i < data.datas.length; i++) {
                    if (data.datas[i].id === comment.id) {
                        index = i;
                        break;
                    }
                }
                data.datas.splice(index, 1);
            }
        })
    },
    checkProject(data, project, check, Message) {
        let param = new FormData()
        param.append("ybProjectItem", JSON.stringify(project))
        param.append("check", check)
        Vue.axios.post("/project/checkProject", param).then(response => {
            if (response.data.code === 200) {
                data.$data.detail = response.data.data
                Message.success("审核通过")
                setTimeout(function () {
                    data.$router.go(-1)
                }, 2000)
            }
        })
    },
    reportProject(data, project, report, Message) {
        let param = new FormData()
        param.append("ybProjectItem", JSON.stringify(project))
        param.append("report", report)
        Vue.axios.post("/project/reportProject", param).then(response => {
            if (response.data.code === 200) {
                data.detail = response.data.data;
                Message.success("报名成功")
            }
        })
    },
    signProject(data, project, Message) {
        let param = new FormData()
        param.append("project", JSON.stringify(project))
        Vue.axios.post("/project/signProject", param).then(response => {
            if (response.data.code === 200) {
                data.detail.status = response.data.data;
                Message.success("签到成功")
            }
        })
    },
    getMessageCount(data) {
        Vue.axios.post("/message/getMessageCount").then(response => {
            if (response.data.code === 200) {
                data.messageNum = Number(response.data.data);
            }
        })
    },
    selectMessage(data) {
        Vue.axios.post("/message/selectMessage").then(response => {
            if (response.data.code === 200) {
                data.messageList = response.data.data
            }
        })
    },
    createMessage(Message, message) {
        let param = new FormData()
        param.append("message", JSON.stringify(message))
        Vue.axios.post("/message/createMessage", param).then(response => {
            if (response.data.code === 200) {
                Message.success("发送成功")
            }
        })
    },
    getProjectItem(data, typeItem, status) {
        let param = new FormData()
        param.append("type_item", JSON.stringify({id: typeItem}))
        param.append("status", status)
        Vue.axios.post("/project/getProjectItem", param).then(response => {
            if (response.data.code === 200) {
                data.data = response.data.data;
            }
        })
    },
    updateBanner(data, Message, banner) {
        let param = new FormData()
        param.append("banners", JSON.stringify(banner))
        Vue.axios.post("/banner/updateBanner", param).then(response => {
            if (response.data.code === 200) {
                Message.success("保存成功")
                setTimeout(() => {
                    window.prompt("luckyjsb://success")
                    data.$router.go(-1);
                }, 2000)
            }
        })
    },
    getJoinProjectItem(data) {
        Vue.axios.post("/project/getJoinProjectItem").then(response => {
            if (response.data.code === 200) {
                data.data = response.data.data
            }
        })
    },
    searchTypeItem(data, search, page, status) {
        let param = new FormData()
        param.append("search", search)
        param.append("page", JSON.stringify(page))
        param.append("status", status)
        Vue.axios.post("/project/searchTypeItem", param).then(response => {
            if (response.data.code === 200) {
                if (page.pageIndex === 0) {
                    data.data = response.data.data;
                } else {
                    if (page.pageIndex === Math.floor(data.data.length / 10)) {
                        response.data.data.forEach(item => {
                            data.data.push(item);
                        });
                        if (response.data.data.length < 10) {
                            data.disable = true;
                        }
                    }
                }
            }
        })
    },
    getProjectReportUser(data, project) {
        let param = new FormData()
        param.append("project", JSON.stringify(project))
        Vue.axios.post("/project/getReportUsers", param).then(response => {
            if (response.data.code === 200) {
                data.userList = response.data.data;
            }
        })
    },
    getUserList(data) {
        Vue.axios.get("/user/getUserList").then(response => {
            data.userList = response.data.data;
        })
    },
    updateUserPower(user) {
        Vue.axios.post("/user/changePower", JSON.stringify(user), {
            headers: {
                'Content-Type': 'application/json'  //如果写成contentType会报错}
            }
        }).then(response => {
            Message.success(response.data.data)
        })
    },
    deleteProjectItem(data, project) {
        console.log(project)
        Vue.axios.post("/project/deleteProjectItem", JSON.stringify(project), {
            headers: {
                'Content-Type': 'application/json'  //如果写成contentType会报错}
            }
        }).then(() => {
            Message.success("删除成功")
            setTimeout(() => {
                data.$data.deleteCheckout = false
                data.$router.go(-1)
            }, 1000)
        })
    },
    updateProjectItem(data, message, callback) {
        data.startTime = timeUtils.dateFormat("yyyy-MM-dd hh:mm:SS", new Date(data.startTime))
        data.endTime = timeUtils.dateFormat("yyyy-MM-dd hh:mm:SS", new Date(data.endTime))
        let param = new FormData()
        param.append("projectItemJson", JSON.stringify(data))
        Vue.axios.post("/project/updateProjectItem", param).then(value => {
            if (value.data.code === 200) {
                callback(value.data.data);
            } else {
                message.error(value.data.data);
                data.startTime = new Date(data.startTime).getTime()
                data.endTime = new Date(data.endTime).getTime()
            }
        }).catch(reason => {
            message.error(reason);
            data.startTime = new Date(data.startTime).getTime()
            data.endTime = new Date(data.endTime).getTime()
        })
    }
}